import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
export default {
	components: {
		uniSegmentedControl
	},
	computed: {
		records() {
			return this.$store.state.accountData.record;
		}
	},
	onLoad() {
		this.chargeRecords =[];
		this.expendRecords =[];
		if(this.records){
			this.records.forEach(res =>{
				if(res.type === '充值'){
					this.chargeRecords.push(res)
				}
				if(res.type === '消费'){
					this.expendRecords.push(res)
				}
			})
		}
		
	},
	data() {
		return {
			items: ['充值记录', '支出记录'],
			current: 0,
			activeColor: '#ff913b',
			styleType: 'text',
			expendRecords: [] ,//支出记录
			chargeRecords:[], //充值记录
		};
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		}
	}
};
